let courr = 60
let setId = null
export default {
    data(){
       return {
            phone:'',
            code:'',
            newpwd:'',
            confirmpwd:'',
            clickText:'获取验证码'
       }
    },
    methods: {
        confirmClick(){
            if(this.phone.length != 11) {
                this.$toast('请输入正确的手机号')
                return false
            }
            if(this.code == '') {
                this.$toast('请输入验证码')
                return false
            }
            if(this.newpwd == '') {
                this.$toast('请输入支付密码')
                return false
            }
            if(this.newpwd != this.confirmpwd) {
                this.$toast('两次输入的密码不一致')
                return false
            }

            this.$http.getresetpaypwd({
                phonecode:this.code,
                paypwd:this.newpwd,
                confirm_pwd:this.confirmpwd
            }).then(res=>{
                if(res.status == 200) {
                    this.$toast(res.mess)
                    setTimeout (()=>{
                        this.getback()
                    },1500)
                }
            })
        },
        getczpaypwdcode(){
            if(this.phone.length != 11) {
                this.$toast('请输入正确的手机号')
                return false
            }
            if(setId) {
                // 禁止重复发送 
                return false
            }
            this.$http.getczpaypwdcode({phone:this.phone}).then(res=>{
                if(res.status == 200) {
                   this.$toast(res.mess)
                   setId = setInterval(()=>{
                       courr--
                       if(courr == 0) {
                           courr = 60
                           clearInterval(setId)
                           this.clickText = '重新获取'
                           
                           return false
                       }
                       this.clickText = courr + 's'
                   },1000)
                }
            })
        },
    },
}
